"use client";

import { useEffect, useRef, useState } from "react";

export default function WhyChooseGrowgo() {
  const [visibleCards, setVisibleCards] = useState<boolean[]>([]);
  const cardRefs = useRef<(HTMLDivElement | null)[]>([]);

  const strengths = [
    {
      title: "Agile Delivery",
      description: "Fast, iterative development cycles ensuring quick time-to-market and continuous improvement.",
      gradient: "from-blue-500 to-cyan-500",
      icon: "‚ö°"
    },
    {
      title: "Tech Expertise",
      description: "Deep knowledge across modern technologies, frameworks, and industry best practices.",
      gradient: "from-purple-500 to-pink-500",
      icon: "üöÄ"
    },
    {
      title: "Innovation Focus",
      description: "Cutting-edge solutions that leverage the latest technological advancements and trends.",
      gradient: "from-green-500 to-teal-500",
      icon: "üí°"
    },
    {
      title: "Customer Centric",
      description: "Dedicated support and personalized solutions tailored to your unique business requirements.",
      gradient: "from-orange-500 to-red-500",
      icon: "‚ù§Ô∏è"
    },
    {
      title: "Proven Reliability",
      description: "Consistent delivery of high-quality solutions with robust testing and quality assurance.",
      gradient: "from-indigo-500 to-purple-500",
      icon: "üõ°Ô∏è"
    }
  ];

  useEffect(() => {
    const observers = cardRefs.current.map((ref, index) => {
      if (!ref) return null;
      
      const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            setVisibleCards(prev => {
              const newVisible = [...prev];
              newVisible[index] = true;
              return newVisible;
            });
          }
        },
        { threshold: 0.3 }
      );
      
      observer.observe(ref);
      return observer;
    });

    return () => {
      observers.forEach(observer => observer?.disconnect());
    };
  }, []);

  return (
    <section className="py-20 px-6 lg:px-12 bg-gradient-to-b from-slate-900 to-gray-900">
      <div className="max-w-7xl mx-auto">
        {/* Section Header */}
        <div className="text-center mb-16">
          <h2 className="text-4xl lg:text-5xl font-bold text-white mb-6">
            Why Choose <span className="bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">Growgo Tech</span>
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Experience the difference with our unique approach to technology solutions and client partnerships
          </p>
        </div>

        {/* Strengths Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6">
          {strengths.map((strength, index) => (
            <div
              key={index}
              ref={el => { cardRefs.current[index] = el; }}
              className={`group relative h-80 lg:h-96 rounded-2xl overflow-hidden cursor-pointer transform transition-all duration-700 ${
                visibleCards[index] 
                  ? 'translate-y-0 opacity-100' 
                  : 'translate-y-10 opacity-0'
              } hover:scale-105 hover:-translate-y-2`}
              style={{ transitionDelay: `${index * 100}ms` }}
            >
              {/* Background Gradient */}
              <div className={`absolute inset-0 bg-gradient-to-b ${strength.gradient} opacity-90 group-hover:opacity-100 transition-opacity duration-300`}></div>
              
              {/* Animated Background Pattern */}
              <div className="absolute inset-0 opacity-20">
                <div className="absolute top-0 left-0 w-full h-full bg-gradient-to-br from-white/20 to-transparent"></div>
                <div className="absolute bottom-0 right-0 w-32 h-32 bg-white/10 rounded-full blur-xl group-hover:scale-150 transition-transform duration-500"></div>
              </div>

              {/* Content */}
              <div className="relative z-10 p-6 h-full flex flex-col justify-between">
                {/* Icon */}
                <div className="text-4xl mb-4 transform group-hover:scale-110 transition-transform duration-300">
                  {strength.icon}
                </div>

                {/* Title and Description */}
                <div className="flex-1 flex flex-col justify-center">
                  <h3 className="text-xl lg:text-2xl font-bold text-white mb-4 group-hover:text-yellow-200 transition-colors duration-300">
                    {strength.title}
                  </h3>
                  <p className="text-white/90 text-sm lg:text-base leading-relaxed group-hover:text-white transition-colors duration-300">
                    {strength.description}
                  </p>
                </div>

                {/* Bottom Accent */}
                <div className="mt-6">
                  <div className="w-12 h-1 bg-white/50 rounded-full group-hover:w-20 group-hover:bg-white transition-all duration-300"></div>
                </div>
              </div>

              {/* Hover Overlay */}
              <div className="absolute inset-0 bg-gradient-to-t from-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </div>
          ))}
        </div>

        {/* Bottom Call to Action */}
        <div className="text-center mt-16">
          <p className="text-lg text-gray-300 mb-6">
            Ready to experience the Growgo Tech advantage?
          </p>
          <button className="bg-gradient-to-r from-blue-600 to-purple-600 text-white px-8 py-4 rounded-full font-semibold text-lg hover:shadow-xl transform hover:scale-105 transition-all duration-300 hover:from-blue-700 hover:to-purple-700">
            Start Your Project
          </button>
        </div>
      </div>
    </section>
  );
}
